
import './App.css';

import Nav from './Components/Nav';
import Search from './Components/Search';
import List from './Components/List';
import Shop from './Components/ShopDetails';
import Identification from './Components/Identification';
import Confirmation from './Components/Confirmation';
import Recap from './Components/Recap';
import RDV from './Components/RDV'
import Communication from './Components/Communication'

import search from './Components/reducers/searchResult.reducer';
import shopDetails from './Components/reducers/shopDetails.reducer';
import token from './Components/reducers/token.reducer'
import ChosenAppointment from './Components/reducers/ChosenAppointment.reducer'
import sendPaiement from './Components/reducers/sendPaiement.reducer'

import { Provider } from 'react-redux';
import { createStore, combineReducers } from 'redux';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';

const store = createStore(
  combineReducers({
    search,
    shopDetails,
    token,
    ChosenAppointment,
    sendPaiement

  })
);

function App() {

  return (


    
    <Provider store={store}>
      <Router>
        <Switch>
    <div className="App color-back">
      <div className="fixed-nav">
      <Nav></Nav>
      </div>
      <div className="App-header">
      <Route component={Search} path="/" exact />
        
      <Route component={List} path="/search" exact />

      <Route component={Shop} path="/shopDetails" exact />

      <Route component={Identification} path="/identification" exact />

      <Route component={Confirmation} path="/confirmation" exact />

      <Route component={RDV} path="/rdv" exact />

      <Route component={Recap} path="/recap" exact />

      <Route component={Communication} path="/communication" exact />


    





      </div>
    </div>
    </Switch>
    </Router>
    </Provider>
  );
}

export default App;
