import React, {useEffect, useState} from 'react'
import {
    Card, CardImg, CardText, CardBody,
    CardTitle, CardSubtitle, Button, 
  } from 'reactstrap';
import { connect } from 'react-redux';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import "../App.css"
import {Link} from 'react-router-dom'
import { Icon } from 'leaflet'

const svgIcon = new Icon({
  iconUrl: '/home-marker.svg',
  iconSize: [50, 50]
  })
  
function List(props) {

  const [shopsData, setShopsData] = useState([]);
 

console.log("ADDRESS", props.search.address)
 
  useEffect(() => {

   // Fetch request from search to BDD
    async function getShops() {
      let shopsFetch = await fetch(`/search`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ data: props.search }),
      });
      let body = await shopsFetch.json();
      console.log("BODY", body)
      setShopsData(body);
      
      
    }

    getShops();

  }, [props.search]);


  var shopsDataTab = shopsData.map(data => {
    return (
      <Card style={{width: "20rem", marginTop: 80, marginRight: 5, marginLeft: 30}} >
      <CardImg top style={{width: "100%", margin: 'auto'}} src={`${data.shopImages[0]}`} />
      <CardBody>
        <CardTitle tag="h5">{data.shopName}</CardTitle>
        <CardSubtitle tag="h6" className="mb-2 text-muted">{data.shopAddress}</CardSubtitle>
        <CardText>{data.shopDescription}</CardText>
        <Link to="/shopDetails">
        <Button onClick={()=>handleShopChoice(data)} style={{backgroundColor: '#4280AB'}}>Choisir ce Salon</Button>
        </Link>
      </CardBody>
    </Card>
    )
  })

//* Marker rendering
let markerTab = shopsData.map(data=>{
return (
  <Marker position={[data.latitude, data.longitude]}>
                <Popup>
                <Card >
                <CardBody >
                  <div style={{display: 'flex', direction: 'row', justifyContent: 'space-around'}}>
                  <div>
                  <CardTitle >{data.shopName} </CardTitle>
                  <CardSubtitle  className="mt-3 text-muted">{data.shopAddress}</CardSubtitle>
                  </div>
                  <CardImg top style={{width: "50%"}} src={`${data.shopImages[0]}`} />
                  </div>
                  
                  <CardText>{data.shopDescription}</CardText>
                  <Link to="/shopDetails">
                  <Button onClick={()=>handleShopChoice(data)} size="sm" style={{backgroundColor: '#4280AB'}}>Choisir ce Salon</Button>
                  </Link>
                </CardBody>
                </Card>
                </Popup>
            </Marker>
)
})


const handleShopChoice =(shopDetails) => {
 
  props.onShopSelection(shopDetails, props.search.utcDate)

}
    
    return (
      <div >
       
        <div style={{display: 'flex', justifyContent: 'center'}}>
        <div style={{ display: 'flex', direction: 'row', flexWrap: 'wrap'}}>
        {shopsDataTab}
      </div>
      <div>
      <div  style={{display: 'flex', justifyContent: "flex-end", marginRight: 100, marginTop: 100, border: "1px solid black"}}>
            <MapContainer id="mapid" center={[48.856614, 2.3522219]} zoom={13} scrollWheelZoom={false}>
            <TileLayer
                attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {markerTab}
            {props.search.address[0] ? 
            <Marker position={[props.search.address[0].longitude, props.search.address[0].latitude] }  icon={svgIcon}>
            <Popup>
              Addresse Choisi
            </Popup>
            </Marker>
            : null }
            </MapContainer>
        </div>
      </div>
      </div>
      </div>
    )
}

function mapStateToProps(state) {
  return { search: state.search };
}

function mapDispatchToProps(dispatch) {
  return {
    onShopSelection: function(details, utcDate) { 
      dispatch({type: 'sendDetails',  
                shopDetails: details,
                utcDate: utcDate
            }) 
              },

  }
}

export default connect(mapStateToProps, mapDispatchToProps)(List);