import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { Jumbotron, Button } from 'reactstrap';
import {Link} from 'react-router-dom'




export const Confirmation = (props) => {
    let appoint = props.ChosenAppointment
    const [paiement, setPaiement] = useState('sur Place')
    //* Reconstitution de la date
       
        let weekday = [
            'Dimanche',
            'Lundi',
            'Mardi',
            'Mercredi',
            'Jeudi',
            'Vendredi',
            'Samedi',
          ][appoint.date.getDay()];
      
          let visualMonth = [
            'Janvier',
            'Fevrier',
            'Mars',
            'Avril',
            'Mai',
            'Juin',
            'Juillet',
            'Aout',
            'Septembre',
            'Octobre',
            'Novembre',
            'Decembre',
          ][appoint.date.getMonth()];
       
          let visualDate = weekday + ' ' + appoint.date.getDate() + ' ' + visualMonth + ' ' + appoint.date.getFullYear()
     
         const [chosenOffer, setChosenOffer ] = useState('');
         const [chosenPrice, setChosenPrice ] = useState('');
        // eslint-disable-next-line
         const [loyaltyPoints, setLoyaltyPoints] = useState(50);
         
useEffect(() => {
  if(appoint.prestation !== null && appoint.prestation !== "Prestation"){
    setChosenOffer(appoint.prestation)
    setChosenPrice(appoint.prestationPrice)

  } else if (appoint.experience !== null && appoint.experience !== "Experience"){
   setChosenOffer(appoint.experience)
   setChosenPrice(appoint.experiencePrice)
 

  }
  // eslint-disable-next-line
}, [chosenOffer])
     let monthForM = appoint.date.getMonth() + 1
     monthForM = monthForM < 10 ? '0' + monthForM : monthForM;
    let dayForM = appoint.date.getDate()
    dayForM = dayForM < 10 ? '0' + dayForM : dayForM;
    
       
let dateForM = appoint.date.getFullYear() + "-" + monthForM + '-' + dayForM + 'T' + appoint.hour + ":00.000+00:00"
console.log("dateForM", dateForM)
 // MONGO: 2021-02-04T09:00:00.000+00:00
      

        let endHour = appoint.hour.split(':')
        endHour.splice(1, 1, 30);
        
        endHour = endHour.join(':')


        let endDateForMongo = appoint.date.getFullYear() + "-" + monthForM + '-' + dayForM + 'T' + endHour + ":00.000+00:00"
      
                console.log("edfm", endDateForMongo)
  
        //        2021-03-19T10:30:00.000Z
console.log("CHOSENAPP", props.ChosenAppointment)

 
          const handleConfirmation = async () => {
           
            await fetch(`/addappointment/${props.token}`, {
              method: 'POST',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({
                chosenOffer: chosenOffer,
                chosenPrice: chosenPrice,
                chosenEmployee: appoint.hairdresser,
                startDate: dateForM,
                endDate: endDateForMongo,
                chosenPayment: 'onshop',
                appointmentStatus: 'validated',
                shop_id: appoint.shopDetailsID,
                loyaltyPoints: loyaltyPoints,
                dateForProfile: visualDate + ' à ' +  appoint.hour
              }),
            });
           
              props.sendPaiement(paiement)
         
          }

        return (
            <div>
            <Jumbotron style={{margin: 100}}>
                <h1 className="display-4 Title-text">Recapitulatif de ton RDV et paiement</h1>
                   
                <hr className="my-2" />
                <div style={{display: "flex", justifyContent: 'space-between' }}>
                    <div style={{textAlign: 'left'}}>
                        <p className="main-text">Nom du salon: <span className="Salon-text">{appoint.shopDetailsName}</span></p>
                        <p className="main-text">Adresse du salon: {appoint.shopDetailsAddress}</p>
                        <p className="main-text">Date et heure: {visualDate}, {appoint.hour}</p>
    
                        {appoint.prestation !== "Prestation" ? 
                        <div>
                            <p className="main-text">Offre Choisi: {appoint.prestation}</p>
                            <p className="main-text">Prix: {appoint.prestationPrice} €</p>
                            <p className="main-text">Durée: {appoint.prestationDuration} Minutes</p>
                        </div>
                        :
                        <div>
                            <p className="main-text">Experience Choisi: {appoint.experience}</p>
                            <p className="main-text">Prix: {appoint.experiencePrice} €</p>
                            <p className="main-text">Durée: {appoint.experienceDuration} Minutes</p>
                        </div>
                        }
                    </div>
    
                    <div>
                        <img style={{width: "20rem"}} src={appoint.shopDetailsImage[0]} alt="shop2"></img>
                    </div>
                    
                </div>
              
    
                <hr className="my-2" />
                <form  >
                    <p className="main-text">Choisis ton Moyen de Paiement:</p>
                    <div  className="main-text" style={{display: "flex", flexDirection: 'column' }}>
                        <div>
                        <input onClick={()=>setPaiement("sur Place")} type="radio" id="sur Place" name="paiement" value="sur Place" checked={true} />
                        <label for="sur Place">Paiement sur Place</label>
                        </div>
                        <div>
                        <input onClick={()=>setPaiement("en Ligne")} type="radio" id="en Ligne" name="paiement" value="en Ligne"  />
                        <label for="en Ligne">Paiement en Ligne</label>
                        </div>
                    </div>
                    
                    
             
                
                <p className="lead">
                <Link to="/recap">
                <Button onClick={()=>handleConfirmation()} style={{marginTop: 20}} type="submit" className="main-text" color="warning">Confirmation du RDV</Button>
                </Link>
                </p>
                </form>
              
          </Jumbotron>
            </div>
        )
}

function mapStateToProps(state) {
    return { ChosenAppointment: state.ChosenAppointment,
              token: state.token };
  }


  function mapDispatchToProps(dispatch) {
    return {
      sendPaiement: function(paiement) { 
        dispatch({type: 'sendPaiement',  
                  paiement: paiement
              }) 
                },
  
    }
  }

export default connect(mapStateToProps, mapDispatchToProps)(Confirmation)
