import React, {useEffect, useState} from 'react'
import { connect } from 'react-redux';
import { Card, Button, CardTitle, CardText, Row, Col, CardImg, Modal, ModalHeader, ModalBody, ModalFooter, Input  } from 'reactstrap';
import { faStar } from '@fortawesome/free-solid-svg-icons'; 
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {Link} from 'react-router-dom'



function RDV({token}) {

    const [modal, setModal] = useState(false);
   
  const [user, setUser] = useState(false);
  const [appointments, setAppointments] = useState([]);
  // eslint-disable-next-line
  const [shops, setShops] = useState([]);
  const [comment, setComment] = useState(null)
  const [rating, setRating] = useState(3)


    useEffect(() => {
        const getUser = async () => {
          const data = await fetch(`/users/myProfile/${token}`);
          const body = await data.json();
    console.log(body.user)
    console.log("SHOPS", body.shops)
    console.log("APP", body.appointments)
          setUser(body.user);
          setShops(body.shops);
          setAppointments(body.appointments);
        };
           getUser();
// eslint-disable-next-line
      }, [token]);


//* handling modal section
const toggle = () => setModal(!modal);

const sendComment = async (shopId, appId) => {
    var newComment = await fetch(`/users/addcomment`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: `comment=${comment}&rating=${rating}&shop_id=${shopId}&token=${token}&appointmentId=${appId}`,
      });
      await newComment.json();
  
}

const handleTextArea = (comment) => {
setComment(comment)
}


var starsTab = [];
for (let i=0; i<5; i++) {
    let starColor = 'black';
    let count = i + 1
  if (i<rating) {
    starColor = 'gold'
  }
  starsTab.push(
    <FontAwesomeIcon
    key={i}
    icon={faStar}
    style={{color: starColor, marginRight: 5, marginBottom: 5}}
    onClick={()=>{setRating(count)}}
  />)
 

}


let futureApp = []
// eslint-disable-next-line
futureApp = appointments.map((app, i) => {
    
    if(new Date(app.startDate) > new Date()){
        
    return(
        <Col sm="3" style={{marginBottom: 30}}>
        <Card body>
        <CardImg top width="100%" src={shops[i].shopImages[0]} alt="Card image cap" />
          <CardTitle tag="h5">{app.chosenOffer}</CardTitle>
          <CardText>Date et heure du rdv : {app.dateForProfile}</CardText>
          <CardText  >Nom du salon:  {shops[i].shopName}</CardText>
            <Link to="/communication">
          <Button  color='outline-info'>Communique avec ton coiffeur</Button>
          </Link>
        </Card>
      </Col>
    )
    }
})


let pastApp = [];
// eslint-disable-next-line
pastApp = appointments.map((app, i) => {
   
    if(new Date(app.startDate) < new Date()){
       
    return(
        <Col sm="3" style={{marginBottom: 30}}>
        <Card body>
        <CardImg top width="100%" src={shops[i].shopImages[0]} alt="Card image cap" />
      
          <CardTitle tag="h5">{app.chosenOffer}</CardTitle>
          <CardText>Date et heure du rdv : {app.dateForProfile}</CardText>
          <CardText >Nom du salon:  {shops[i].shopName}</CardText>
        
          <Button onClick={toggle} color="outline-danger">Ecris un avis</Button>
          
            <Modal isOpen={modal} toggle={toggle} >
                <ModalHeader toggle={toggle}>{shops[i].shopName}</ModalHeader>
                <ModalBody>
                    {starsTab}
                <Input placeholder="Laisse un avis" onChange={(e)=>handleTextArea(e.target.value)} type="textarea"></Input>
                </ModalBody>
                <ModalFooter>
                    <Button color="primary" onClick={()=>{toggle(); sendComment(shops[i]._id, app._id)}}>Publie ton avis</Button>{' '}
                    <Button color="secondary" onClick={toggle}>Non, merci!</Button>
                </ModalFooter>
            </Modal> 

        </Card>
      </Col>
    )
    }
})






    return (
        <div style={{marginTop: 100}}>
           <h3 className="main-text" > Bienvenue dans ton historique de RDV </h3>
           <h1 className="Title-text" style={{marginBottom: 50}}> {user.firstName} {user.lastName} ! </h1>

            <div style={{borderRadius: 10, backgroundColor: '#4280AB', width: "25%", padding: 10, display: 'flex', justifyContent: 'center', marginLeft: 20}}>
           <h3 className="main-text">Mes RDV à Venir : </h3>
           </div>
           <Row style={{margin: 10}}>
            {futureApp}   
     
      
    </Row>
    
            <div style={{borderRadius: 10, backgroundColor: '#AB4242', width: "25%", padding: 10, display: 'flex', justifyContent: 'center', marginLeft: 20}}>
           <h3 className="main-text">Mes RDV Passés: </h3>
           </div>
           <Row style={{margin: 10}}>
               {pastApp}
            
    </Row>
        </div>
    )
}

const mapStateToProps = (state) => {
    return { token: state.token };
  };
  export default connect(mapStateToProps, null)(RDV);
  
