import React, { useState } from 'react';
import { Button, Input, Card, Form } from 'reactstrap'
import { connect } from 'react-redux'
import '../App.css'
import { Redirect } from 'react-router';

function Identification(props) {
    
    const [redirection, setRedirection ] = useState(false);

//*States for Signin IN

    const [signInEmail, setSigninEmail] = useState('')
    const [signInPassword, setSignInPassword] = useState('')

    const [passwordError, setPasswordError] = useState(null);
    const [emailError, setEmailError] = useState(null);

//*States for Signin UP
    const [signUpEmail, setSignUpEmail] = useState('');
    const [signUpPhoneNumber, setSignUpPhoneNumber] = useState('');
    const [signUpFirstName, setSignUpFirstName] = useState('');
    const [signUpLastName, setSignUpLastName] = useState('');
    const [signUpPassword, setSignUpPassword] = useState('');

    const [signUpPasswordError, setSignUpPasswordError] = useState(null);
    const [signUpemailError, setSignUpEmailError] = useState(null);
  const [inputError, setInputError] = useState(null);


//* HANDLE BUTTON SIGN IN - fetch data from back to check if user exists
        const handleSignIn =  async () => {
          
            const data = await fetch(`/users/signIn`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                email: signInEmail,
                password: signInPassword,
            }),
            });
            const body = await data.json();
        
            if (!body.result) {
                setEmailError(body.emailNotFound);
                setPasswordError(body.invalidPassword);
            } else {
                props.onAddToken(body.token);
            setRedirection(true)
            }
        }
        
        if(redirection && props.ChosenAppointment.hour){
            return (
                <Redirect to='/confirmation' />
            )
        } else if(redirection && props.ChosenAppointment.hour === undefined){
          return (
            <Redirect to='/' />
        )
        }
      
//* Handle Sign UP button
        
        const handleSignUp = async () => {
            const data = await fetch(`/users/signUp`, {
              method: 'POST',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({
                firstName: signUpFirstName,
                lastName: signUpLastName,
                phoneNumber: signUpPhoneNumber,
                email: signUpEmail,
                password: signUpPassword,
              }),
            });
        
            const body = await data.json();
         
        
            if (!body.result) {
              setSignUpEmailError(body.emaiExist);
              setSignUpPasswordError(body.invalidPassword);
            } else {
              props.onAddToken(body.token);
              setRedirection(true)
             
            }
        
            if (body.error) {
              setInputError(body.error);
            } else {
              setInputError(null);
            }
          }
        



    return (
        <div style={{marginTop: 10, display: 'flex', justifyContent: 'center'}}>
             <div style={{margin: 100}} className="identification">
             <Card style={{padding: 30}}>
                
                <h4 className="main-text">Identification</h4>  
                <Form>
                    <Input value={signInEmail} onChange={e=>setSigninEmail(e.target.value)} type="email" style={{marginBottom: 20}} placeholder="Mail"/>
                    {emailError}
                    <Input value={signInPassword} onChange={e=>setSignInPassword(e.target.value)} type="password" style={{marginBottom: 20}} placeholder="Mot de Passe"/>
                    {passwordError}
                    <Button className="main-text" onClick={()=>handleSignIn()} style={{backgroundColor:"#4280AB", width: "100%"}}>Sign In</Button> 
                </Form>

            </Card>

            </div>

            <div style={{margin: 100}} className="identification">
                <Card style={{padding: 30}}>

                <h4 className="main-text">Inscription</h4>         
                <p style={{color: 'red'}}>{inputError}</p>
                <Input value={signUpFirstName}
                onChange={e=>setSignUpFirstName(e.target.value)} 
                type="text" 
                style={{marginBottom: 20}} 
                placeholder="Prénom"/>

                <Input value={signUpLastName} 
                onChange={e=>setSignUpLastName(e.target.value)} 
                type="text" 
                style={{marginBottom: 20}} 
                placeholder="Nom"/>

                <Input value={signUpPhoneNumber} 
                onChange={e=>setSignUpPhoneNumber(e.target.value)} 
                type="number" 
                style={{marginBottom: 20}} 
                placeholder="Téléphone"/>


                <Input value={signUpEmail} 
                onChange={e=>setSignUpEmail(e.target.value)} 
                type="email" 
                style={{marginBottom: 20}} 
                placeholder="Mail"/>  
                {signUpemailError}

                <Input value={signUpPassword} 
                onChange={e=>setSignUpPassword(e.target.value)}
                type="password" 
                style={{marginBottom: 20}} 
                placeholder="Mot de Passe"/>  
                {signUpPasswordError}

                <Button className="main-text" onClick={()=>handleSignUp()} style={{backgroundColor:"#4280AB", width: "100%"}}>Sign Up</Button>     
                </Card>
            </div>
          
        </div>
    )
}

function mapStateToProps(state) {
  return { ChosenAppointment: state.ChosenAppointment };
}


const mapDispatchToProps = (dispatch) => {
    return {
      onAddToken: (token) => {
        dispatch({ type: 'ADD_TOKEN', token,
       });
      },
    };
  };

  
  export default connect(mapStateToProps, mapDispatchToProps)(Identification);