import React, {useState, useEffect} from 'react'
import {   Button, Label, Input, ListGroup, ListGroupItem,
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselCaption,
    
    } from 'reactstrap';
import { fr } from 'date-fns/locale'
import DatePicker, {registerLocale, setDefaultLocale } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faClock, faCalendarAlt } from '@fortawesome/free-solid-svg-icons'; 
import {connect} from 'react-redux';
import {Link} from 'react-router-dom'
    



    

      const  Search = (props) => {
      
        const [activeIndex, setActiveIndex] = useState(0);
        

        const [startDate, setStartDate] = useState(null);
        const [startTime, setStartTime] = useState(null);
       

       
        const [ position, setPosition] = useState({latitude : null, longitude : null})


        const [prestation, setPrestation] = useState(null)
        const [experience, setExperience] = useState(null)
        const [disabled, setDisabled] = useState(true)
       

const items = [
  {
    src: 'https://images.unsplash.com/photo-1516980907201-943c13a8d03c?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80',
    altText: 'MOMENT A DEUX',
    caption: 'MOMENT A DEUX'
  },
  {
    src: 'https://images.unsplash.com/photo-1602789216385-c6f910f9b450?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80',
    altText: 'PLAY HARD CUT HARD',
    caption: 'PLAY HARD CUT HARD'
  },
  {
    src: 'https://images.unsplash.com/photo-1583416750470-965b2707b355?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80',
    altText: 'BIEN ETRE',
    caption: 'BIEN ETRE'
  },
  {
  src: 'https://images.unsplash.com/photo-1551024709-8f23befc6f87?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80',
    altText: 'APERO COIF',
    caption: 'APERO COIF'
  }
];  

       //* set french time and region
        registerLocale('fr', fr)
        setDefaultLocale('fr');


        //* carousel arrows animation
         const next = () => {
      
          const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
          setActiveIndex(nextIndex);
        }
      
        const previous = () => {
          
          const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
          setActiveIndex(nextIndex);
        }
      
        const handleModal = (caption) => {
         
          setExperience(caption)
          setDisabled(false)
        }
      
        const slides = items.map((item) => {
          return (
            
            <CarouselItem
              
              key={item.src}
             
            >
              <img  onClick={()=>handleModal(item.caption)} style={{width: "23rem", borderRadius: 15}} src={item.src} alt={item.altText} />
             
              <Button style={{backgroundColor: 'transparent', border: 0}}  onClick={()=>handleModal(item.caption)}>
              <CarouselCaption className="Title-text" captionText={item.caption}  />
              </Button>
            </CarouselItem>
          );
        });

        const prestationArr = ["Coupe Homme", "Coupe Femme", "Coupe Homme + Barbe", "Coloration", "Brushing", 'Balayage']


        const prestaTab = prestationArr.map((prestation) => { 
          
           return <option value={prestation}>{prestation}</option>
        })

        const handlePrestation = (e) => {
         
          setPrestation(e.target.value)
          setExperience(null)
        }
       
        const [ adressSearch, setAdressSearch ] = useState('')
        const [ adressVisible, setAdressVisible ] = useState(false)
        const [ address, setAddress] = useState([])

        let addressSugg = []
        
        
        const onChangeAdress = (arg) => {
            setAdressSearch(arg);
            
          }

            useEffect( () => {
              
              async function fetchData() {
              if(adressSearch.length > 3) {
                setAdressVisible(true)
              var rawResponse = await fetch(`https://api-adresse.data.gouv.fr/search/?q=${adressSearch}&autocomplete=1`)
              var response = await rawResponse.json()
                            
              response.features.map(sugg => {
                return(
                      addressSugg.push({
                          label: sugg.properties.label,
                          latitude: sugg.geometry.coordinates[0],
                          longitude: sugg.geometry.coordinates[1]
                      }))
              })
              
              setAddress(addressSugg);   
            
            }
          }
          fetchData();
          // eslint-disable-next-line
            }, [adressSearch])
               
     
      const handleConfirmation = () => {
   

          //*Recomposer la date au bon format et la bonne langue
          let completeDate = null;
          let chosenTime = null
          const dateEvent = new Date(startDate);
          const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
          completeDate = dateEvent.toLocaleDateString('fr-FR', options)
          setExperience(null)

          //* Renvoi de l'horaire choisi au bon format
          const timeEvent = new Date(startTime)
          chosenTime = timeEvent.toLocaleTimeString('fr-FR', {hour: '2-digit', minute:'2-digit'})
          // console.log("TIME", timeEvent)
          // console.log("TIME", dateEvent)

          //* Renvoi des coordonées de l'adresse ou la géoloc
         
   props.onSubmitSearch(completeDate, dateEvent, chosenTime, address, position, prestation, experience)


      }


      const handleExperience = () =>{
           //*Recomposer la date au bon format et la bonne langue
           let completeDate = null;
           let chosenTime = null
           const dateEvent = new Date(startDate);
           const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
           completeDate = dateEvent.toLocaleDateString('fr-FR', options)
           
           
           
 
           //* Renvoi de l'horaire choisi au bon format
           const timeEvent = new Date(startTime)
           chosenTime = timeEvent.toLocaleTimeString('fr-FR', {hour: '2-digit', minute:'2-digit'})
           // console.log("TIME", timeEvent)
           // console.log("TIME", dateEvent)
 
           //* Renvoi des coordonées de l'adresse ou la géoloc
           setPrestation(null)
    props.onSubmitSearch(completeDate, dateEvent, chosenTime, address, position, prestation, experience)
       
       
    }
   


    return (
        <div style={{display: "flex", justifyContent:'space-evenly', direction: "row", marginLeft: "10vw", marginTop: 50 }} >
              
            <div  style={{ marginTop: "2.5rem", justifyContent:'center', direction: "row", }}>
                <p className="Title-text">Recherchez votre coiffeur: </p>
                    <div style={{ marginBottom: 10, backgroundColor: 'white', padding: 30, borderRadius: 15}} >
                     
                      

                       <div>
                            <Label className="main-text" style={{margin: 8}}>Date:</Label>
                            <DatePicker 
                            locale="fr" 
                            minDate={new Date()} selected={startDate} 
                            onChange={date => setStartDate(date)} 
                            dateFormat=" d MMMM yyyy"
                            todayButton="Aujourd'hui"/>
                            <FontAwesomeIcon style={{marginLeft: 5}}  icon={faCalendarAlt} size="lg"/>

                        </div>

                        <div style={{marginTop: 20}}>
                            <Label className="main-text" style={{margin: 8}}>Heure:</Label>
                            <DatePicker
                                selected={startTime}
                                onChange={date => setStartTime(date)}
                                showTimeSelect
                                // minTime={startDate}
                                showTimeSelectOnly
                                timeIntervals={30}
                                timeCaption="Heure"
                                dateFormat="h:mm aa"
                                
                                />
                            <FontAwesomeIcon style={{marginLeft: 5}} icon={faClock} size="lg"/>

                        </div>
                        <div style={{width: "25vw"}}>
                            <p className="main-text" style={{margin: 8}}>Addresse:</p>
                            <div style={{display: 'flex', direction: 'row', alignItems: "center"}}>
                            <Input
                                type='text'
                                placeholder='Saisir une adresse'
                                value={adressSearch} 
                                onChange={(e) => onChangeAdress(e.target.value)}
                                style={{}} /> 
                         
                         
                            </div>
                            
                       
                             <ListGroup>
                                 {adressVisible ? address.map((e, key) => { return(

                                    <ListGroupItem className="justify-content-between addresses" key={key} style={{ backgroundColor: "white", color: "black"}}  value={e.label} onClick={() => {setAdressSearch(e.label); setPosition({latitude: e.latitude, longitude: e.longitude}); setAdressVisible(false)}}>  {e.label}</ListGroupItem>
                                                        
                               
                                )
                            }) : null}
       
                            </ListGroup>
                            </div>

                        <div style={{ marginTop: 10, width: "25vw"}}>
                            <Label className="main-text" style={{margin: 8}}>Prestation:</Label>
                            <Input type="select" onChange={(e)=>handlePrestation(e)} name="prestation" id="prestation">
                                {prestaTab}
                            </Input>
                        </div>

                    </div>
                    {(startDate && startTime && address) ?
                        <Link to="/search">
                          <Button  onClick={()=> handleConfirmation()} style={{backgroundColor:"#4280AB", fontFamily: "graduate", color: 'black', width: "100%"}}>Recherche</Button>
                        </Link>
                    :
                    <Button  disabled style={{backgroundColor:"#4280AB", fontFamily: "graduate", color: 'black', width: "100%"}}>Choisir une date et une heure pour poursuivre</Button>
                    }
            </div>
            
            <div  style={{padding: 50, width: "50vw"}}>
            <p className="Title-text">Vivez une experience unique : </p>
                <Carousel
                    activeIndex={activeIndex}
                    next={next}
                    previous={previous}
                    >             
                    
                    {slides}
                    <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
                    <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
                </Carousel>
                {experience ? <p className="Title-text">{experience}</p> : null}
                {!disabled && startDate && startTime ? 
                <Link to="/search">
                <Button onClick={()=>handleExperience()} style={{backgroundColor:"#AB4242", marginTop: 15, fontFamily: "graduate", color: 'black', width: "75%"}}>Vivre cette expérience</Button>
                </Link> 
                :
                 <Button disabled onClick={()=>handleExperience()} style={{backgroundColor:"#AB4242", marginTop: 15, fontFamily: "graduate", color: 'black', width: "75%"}}>Choisr une experience, une date et une heure</Button> 
                
                          }
                  
            </div>
            
        </div>
        
    )
}


function mapStateToProps(state) {
  return { token: state.token };
}


function mapDispatchToProps(dispatch) {
  return {
    onSubmitSearch: function( completeDate, utcDate, hour, address, position, offer, experience) { 
      dispatch({type: 'createSearch',  
                completeDate : completeDate,
                utcDate: utcDate,
                hour : hour, 
                address : address, 
                position : position, 
                offer : offer, 
                experience : experience}) 
              },

  }
}



export default connect(
  mapStateToProps, 
    mapDispatchToProps
)(Search);