import React from 'react';
import { Jumbotron, Button } from 'reactstrap';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom'




function Confirmation(props) {
    let appoint = props.ChosenAppointment
    console.log("PAIEMENT", props.sendPaiement.paiement)
//* Reconstitution de la date
   
    let weekday = [
        'Dimanche',
        'Lundi',
        'Mardi',
        'Mercredi',
        'Jeudi',
        'Vendredi',
        'Samedi',
      ][appoint.date.getDay()];
  
      let month = [
        'Janvier',
        'Fevrier',
        'Mars',
        'Avril',
        'Mai',
        'Juin',
        'Juillet',
        'Aout',
        'Septembre',
        'Octobre',
        'Novembre',
        'Decembre',
      ][appoint.date.getMonth()];
      console.log(month)
      console.log(weekday)
      console.log(appoint.date.getDate())
      let visualDate = weekday + ' ' + appoint.date.getDate() + ' ' + month + ' ' + appoint.date.getFullYear()

    console.log(props.ChosenAppointment)
    return (
        <div>
        <Jumbotron style={{margin: 100}}>
            <h1 className="display-4 Title-text">Super! C'est confirmé!</h1>
            <p className="lead">Merci pour ton soutien!</p>
            <p className="lead">Tu recevra un SMS un jour avant ton RDV</p>

            <hr className="my-2" />
            <div style={{display: "flex", justifyContent: 'space-between' }}>
                <div style={{textAlign: 'left'}}>
                    <p className="main-text">Nom du salon: <span className="Salon-text">{appoint.shopDetailsName}</span></p>
                    <p className="main-text">Adresse du salon: {appoint.shopDetailsAddress}</p>
                    <p className="main-text">Date et heure: {visualDate}, {appoint.hour}</p>
                    <p className="main-text">Moyen de paiement choisi: {props.sendPaiement.paiement}</p>
                    {appoint.prestation !== "Prestation" ? 
                    <div>
                        <p className="main-text">Offre Choisi: {appoint.prestation}</p>
                        <p className="main-text">Prix: {appoint.prestationPrice} €</p>
                        <p className="main-text">Durée: {appoint.prestationDuration} Minutes</p>
                    </div>
                    :
                    <div>
                        <p className="main-text">Experience Choisi: {appoint.experience}</p>
                        <p className="main-text">Prix: {appoint.experiencePrice} €</p>
                        <p className="main-text">Durée: {appoint.experienceDuration} Minutes</p>
                       

                    </div>
                    }
                </div>

                <div>
                    <img style={{width: "20rem"}} src={appoint.shopDetailsImage[0]} alt="shop"></img>
                </div>
                
            </div>
          

            <hr className="my-2" />
            <p className="lead">
            <h2 className="display-4 Title-text">Aller plus loin: </h2>
            <Link to="/communication">
            <Button style={{backgroundColor: "#AB4242"}} className="main-text"  size="lg" >Communique avec ton coiffeur</Button>
            </Link>
            </p>
          
      </Jumbotron>
        </div>
    )
}

function mapStateToProps(state) {
    return { ChosenAppointment: state.ChosenAppointment,
             sendPaiement: state.sendPaiement        
    };
  }

  export default connect(mapStateToProps, null)(Confirmation);
