import React, {useState} from 'react';
import { Redirect} from 'react-router-dom'
import { connect } from 'react-redux';
import { 
    Jumbotron, 
    Button,  
    Carousel, CarouselItem, CarouselControl,
    ListGroup, ListGroupItem,
    Dropdown, DropdownToggle, DropdownMenu, DropdownItem
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar, faWheelchair, faGlassMartini, faPaw, faLeaf, faGamepad, faCoffee, faEuroSign } from '@fortawesome/free-solid-svg-icons'; 



function ShopDetails(props) {

    const [activeIndex, setActiveIndex] = useState(0);

    const [chosenTime, setChosenTime ] = useState(null);

    const [dropdownOpen1, setDropdownOpen1] = useState(false);
    const [dropdownOpen2, setDropdownOpen2] = useState(false);
    const [dropdownOpen3, setDropdownOpen3] = useState(false);

    // const [shopFromDB, setShopFromDB ] = useState()

    const toggle1 = () => setDropdownOpen1(prevState => !prevState);
    const toggle2 = () => setDropdownOpen2(prevState => !prevState);
    const toggle3 = () => setDropdownOpen3(prevState => !prevState);


    let shop = props.shopDetails.shopDetails
    let utcDate = props.shopDetails.utcDate


    // useEffect(() => {
    //     async function getShops() {
    //         let shopsFetch = await fetch(`/getShops`);
    //         let body = await shopsFetch.json();
        
    //         setShopFromDB(body)
            
            
    //       }
      
    //       getShops();
          
         
    //         }, [])
    

//*Carousel 
    const next = () => {
        const nextIndex = activeIndex === shop.shopImages.length - 1 ? 0 : activeIndex + 1;
        setActiveIndex(nextIndex);
    }

    const previous = () => {
        const nextIndex = activeIndex === 0 ? shop.shopImages.length - 1 : activeIndex - 1;
        setActiveIndex(nextIndex);
    }

    const slides = shop.shopImages.map((item) => {
        return (
        <CarouselItem key={item} >
            <img  style={{width: "40rem", borderRadius: 15, margin: 40}} src={item} alt={shop.shopName} />
        </CarouselItem>
        );
    });

//* Stars Rating
    var starTab = [];
    var roundedStarRating = Math.round(shop.rating);
    var decimalStarRating = Math.round(shop.rating * 10) / 10
    for (let i = 0; i < 5; i++) {
      var starColor = 'black';
      if (i < roundedStarRating) {
        starColor = 'gold';
      }
      starTab.push(
        <FontAwesomeIcon
          key={i}
          icon={faStar}
          style={{color: starColor, marginRight: 5}}
        />
      );
    }
    
//* Picto

    var pictoTab = [];
    for( let i = 0; i < shop.shopFeatures.length; i++){
        let picto = shop.shopFeatures[i];
      if (picto === 'wheelchair-alt') {
        pictoTab.push(<FontAwesomeIcon icon={faWheelchair} style={{marginRight: '5'}}/>)
      } else if (picto === 'glass') {
        pictoTab.push(<FontAwesomeIcon icon={faGlassMartini} style={{marginRight: '5'}}/>)
      } else if (picto === 'paw') {
        pictoTab.push(<FontAwesomeIcon icon={faPaw} style={{marginRight: '5'}}/>)
      } else if (picto === 'leaf'){
        pictoTab.push(<FontAwesomeIcon icon={faLeaf} style={{marginRight: '5'}}/>)
      } else if (picto === 'gamepad') {
        pictoTab.push(<FontAwesomeIcon icon={faGamepad} style={{marginRight: '5'}}/>)
      } else {
        pictoTab.push(<FontAwesomeIcon icon={faCoffee} style={{marginRight: '5'}}/>)
      }
    }

//* Price Fork

var priceTab = [];
for(let i = 0; i < 3; i++){
    var euroColor = 'grey';
    if (i < shop.priceFork) {
      euroColor = 'black';
    }
    priceTab.push(
      <FontAwesomeIcon
        key={i}
        icon={faEuroSign}
        style={{ marginRight: 5, color: euroColor }}
        size={24}
      />
    );
}


//* Comment Section

var listCommentItem = shop.comments.map((l, i) => {
    //* mapping on stars for comments:
    var starCommentTab = [];
    var commentStarRating = Math.round(l.rating);
    
    for (let i = 0; i < 5; i++) {
      var starColor = 'black';
      if (i < commentStarRating) {
        starColor = 'gold';
      }
      starCommentTab.push(
        <FontAwesomeIcon
          key={i}
          icon={faStar}
          style={{color: starColor, marginRight: 5}}
        />
      );
    }
    return (
      <div style={{display: 'flex',justifyContent: 'center'}}>
      <ListGroup style={{width: '40%', marginBottom: 5}} key={i} >
          <ListGroupItem>
            {starCommentTab} 
          </ListGroupItem>

          <ListGroupItem>
              {l.comment}
          </ListGroupItem>
      </ListGroup>
      </div>
    );
  });


//* Time Appointment Picker
        
        //?Converting minutes from backend to hour display
        const convertMinsToTime = (mins) => {
            let hours = Math.floor(mins / 60);
            let minutes = mins % 60;
            hours = hours < 10 ? '0' + hours : hours;
            minutes = minutes < 10 ? '0' + minutes : minutes;
            return `${hours}:${minutes}`;
        };
       
        //?getting day of the week from utcdate to compare with schedule day of the week from backend
        let dayFromSearch =  [
            'Sunday',
            'Monday',
            'Tuesday',
            'Wednesday',
            'Thursday',
            'Friday',
            'Saturday',
          ][new Date(utcDate).getDay()];


       //?Mapping and forlooping to send back schedule in form of clickable buttons so user can choose  a schedule
        let schedule = [];
        let appointDate = []
     

        for(let k = 0; k < shop.appointments.length; k++){ 
            appointDate.push(new Date(shop.appointments[k].startDate))
         }
        

        let newUtcDate = utcDate.getDay() + ' ' + utcDate.getMonth() + ' ' + utcDate.getFullYear()
        let newAppointDate =[]
        // eslint-disable-next-line
        appointDate.map(element => { newAppointDate.push(element.getDay()  + ' ' + element.getMonth() + ' ' + element.getFullYear())})
        let appointmentsToCompare = []
        
        for(let i = 0; i < newAppointDate.length; i++){
            if(newAppointDate[i] === newUtcDate){
            appointmentsToCompare.push(new Date(appointDate[i]).getMinutes() + new Date(appointDate[i]).getHours() * 60)
            }
        }

        for(let i = 0; i < shop.schedule.length; i++){
            
            if(shop.schedule[i].dayOfTheWeek === dayFromSearch){
                
                for(let j = shop.schedule[i].openingHours; j <= shop.schedule[i].closingHours; j+=30) { 
                                       
                      if(appointmentsToCompare.includes(j)){
                        schedule.push(<Button onClick={()=>handleReservation(j)} key={j} disabled color="secondary" style={{margin: 5}}>{convertMinsToTime(j - 60)}</Button>)
                      }else {
                        schedule.push(<Button onClick={()=>handleReservation(j-60)} key={j} color="warning" style={{margin: 5}}>{convertMinsToTime(j - 60)}</Button>)
                      }
                     
                }
            }
        }

            //? schedule was doubled because of doubld "forLoops", had to use Set on array of object to remove duplicates
            const finalSchedule = Array.from(new Set(schedule.map(a => a.key)))
            .map(key => {
            return schedule.find(a => a.key === key)
            })
 

const handleReservation = (chosenTime) =>{
    
            setChosenTime(chosenTime)
         }
       
      
//* Setting up Three DropDowns for coiffeur, offer and experience
       //? initialisation des états
         const [coiffeur, setCoiffeur ] = useState(null);

         const [experience, setExperience ] = useState(null);
         const [experiencePrice, setExperiencePrice] = useState();
         const [experienceDuration, setExperienceDuration] = useState(0);

         const [prestation, setPrestation ] = useState(null);
         const [prestaPrice, setPrestaPrice] = useState();
         const [prestaDuration, setPrestaDuration] = useState(0);

           //?Coiffeur
         const handleCoiffeurSelection = (i) => {
             setCoiffeur(i)
         }

         if(coiffeur === null){
             setCoiffeur("Pas de Préférence")
         }

         let coiffeurTab = shop.shopEmployees.map(employee=>{
             return (
                <DropdownItem  onClick={()=>handleCoiffeurSelection(employee)}>{employee}</DropdownItem>
             )
         })
           //?Prestation Offre
         const handlePrestationSelection = (type, price, duration) => {
             setPrestation(type)
           setExperience(null)
           setMessage(null)
           setPrestaPrice(price)
           setPrestaDuration(duration)
             
         }

         if(prestation === null){
             setPrestation("Prestation")
         }

         let prestationTab = shop.offers.map(offers=>{
            return (
               <DropdownItem onClick={()=>handlePrestationSelection(offers.type, offers.price, offers.duration)}><div style={{display: 'flex', justifyContent: 'space-between', width: 350}}><p>{offers.type}</p><p>{offers.price}€</p></div></DropdownItem>
            )
        })
       

         //?Experience
         const handleExperienceSelection = (type, price, duration) => {
             setExperience(type)
             setPrestation(null)
             setMessage(null)
             setExperiencePrice(price)
             setExperienceDuration(duration)
            
         }

         if(experience === null){
             setExperience("Experience")
         }
         
         let experienceTab = shop.packages.map(packages=>{
            return (
               <DropdownItem  onClick={()=>handleExperienceSelection(packages.type, packages.price, packages.duration)}><div style={{display: 'flex', justifyContent: 'space-between', width: 250}}><p>{packages.type}</p><p>{packages.price}€</p></div></DropdownItem>
            )
        })


//* Handle Final Confirmation, sending info to finalreducer
        const [message, setMessage ] = useState(null);
        const [ prestaIsChosen, setPrestaIsChosen ] = useState(false)
        
        const handleConfirmation = () => {
         
          if(((prestation !== "Prestation" && experience === "Experience") 
          || 
          (prestation === "Prestation" && experience !== "Experience")) 
          && (chosenTime !== null)){
               setPrestaIsChosen(true)
               
           } else {
               setMessage(<p style={{color: 'red', fontWeight:'bold'}}>Choisir une offre et un horaire </p>)
           }

      
          
           props.chosenAppointment(
            convertMinsToTime(chosenTime),
            coiffeur,
            prestation,
            prestaPrice,
            prestaDuration,
            experience,
            experiencePrice,
            experienceDuration,
            utcDate,
            shop.shopName,
            shop.shopAddress,
            shop._id,
            shop.shopImages,)
        }

        if(prestaIsChosen && props.token === ''){
            return (
                <Redirect to='/identification' />
            )
        } else if(prestaIsChosen && props.token !== ''){

            return (
                <Redirect to='/confirmation' />
            )
        }



//!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!RETURN!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!//

    return (
        <div style={{marginTop: 50}}>
        <div style={{display: 'flex', justifyContent: 'center'}}>
                 <Carousel
                    activeIndex={activeIndex}
                    next={next}
                    previous={previous}
                    >             
                    
                    {slides}
                    <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
                    <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
                </Carousel>

        <Jumbotron style={{width: "80%", margin: 40}}>
            <h1 className="display-4">{shop.shopName}</h1>
            <p >{shop.shopDescription}</p>
            <hr className="my" />
            
            <p>{shop.shopAddress}</p>
            <p>Fourchette de Prix: {priceTab} </p>
            <p>Services: {pictoTab} </p>
            <p>Note: {starTab}({decimalStarRating}/5)  </p>
            <a  href="#avis">Voir les {shop.comments.length} avis de "{shop.shopName}"</a>

            <p className="lead">
                <div style={{display: 'flex', justifyContent: 'space-evenly', alignItems: 'center' }}>
                <div style={{marginTop: 10, padding: 5}}>
                    <p>Coiffeur: </p>
                    <Dropdown  direction="left" isOpen={dropdownOpen1} toggle={toggle1}>
                        <DropdownToggle color="info"  caret>
                            {coiffeur}
                        </DropdownToggle>
                        <DropdownMenu>
                            {coiffeurTab}
                        <DropdownItem value="Pas de Préférence" onClick={(e)=>handleCoiffeurSelection(e.target.value)}>Pas de Préférence</DropdownItem>
                        </DropdownMenu>
                    </Dropdown>
                </div>
                <div style={{marginTop: 10}}>
                    <p>Offre: </p>
                    <Dropdown isOpen={dropdownOpen2} toggle={toggle2}>
                        <DropdownToggle color="info"  caret>
                            {prestation}
                        </DropdownToggle>
                        <DropdownMenu>
                            {prestationTab}
                        </DropdownMenu>
                    </Dropdown>
                </div>
                <div style={{marginTop: 10}}>
                <p>Experiece: </p>
            <Dropdown direction="left" isOpen={dropdownOpen3} toggle={toggle3}>
                <DropdownToggle color="info" caret>
                {experience}
                </DropdownToggle>
                <DropdownMenu>
                {experienceTab}
                </DropdownMenu>
                </Dropdown>
                </div>
                </div>
            <hr className="my" />
            <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center'  }}>
            {finalSchedule}

            </div>
          
            {message}
            <Button onClick={()=>handleConfirmation()} style={{marginTop: 10, backgroundColor: "#4280AB", width: "20rem"}}>Confirmer</Button>
            
            </p>
           
        </Jumbotron>
       
        </div>
        
        <div id="avis" >
        <p className="Title-text" style={{marginTop: 20}}>Ce qu'en pensent les Clients:</p>
        {listCommentItem}
        </div>
        </div>
    )
}
function mapDispatchToProps(dispatch) {
    return {
      chosenAppointment: function (
        hour,
        coiffeurs,
        quoi,
        price,
        duration,
        experience,
        experiencePrice,
        experienceDuration,
        date,
        shopDetailsName,
        shopDetailsAddress,
        shopDetailsID,
        shopDetailsImage,
      ) {
        dispatch({
          type: 'finalAppointment',
          hour: hour,
          hairdresser: coiffeurs,
          prestation: quoi,
          prestationPrice: price,
          prestationDuration: duration,
          experience: experience,
          experiencePrice: experiencePrice,
          experienceDuration: experienceDuration,
          date: date,
          shopDetailsName: shopDetailsName,
          shopDetailsAddress: shopDetailsAddress,
          shopDetailsID: shopDetailsID,
          shopDetailsImage: shopDetailsImage
        });
      },
    };
}
function mapStateToProps(state) {
    return { shopDetails: state.shopDetails,
            token: state.token };
  }
  
  
  
  export default connect(mapStateToProps, mapDispatchToProps)(ShopDetails);
