import '../App.css';
import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  NavLink,
  Nav, NavItem
} from 'reactstrap'
import {connect} from 'react-redux';
import { Link} from 'react-router-dom'
 

function NavBar(props) {

    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);

 


    


    return (
        <div>
            <Navbar style={{display: 'flex', justifyContent: 'space-between'}}  className="color-back fixed-nav sticky"  light expand="md">
               
                <div>
                     <NavbarBrand href="/" className="Title-text" ><h4>'Stach</h4></NavbarBrand>
                </div>

                <div>
                    <NavbarToggler onClick={toggle} />
                    <Collapse  isOpen={isOpen} navbar>  
                        {isOpen ? 
                                    <Nav navbar > 
                                        <NavItem>
                                            {props.token === ''?
                                                <NavLink className="main-text" href="/identification">Se Connecter/S'inscrire</NavLink>
                                                :
                                                <div style={{justifyContent: 'center'}}>
                                                    <Link to='/rdv'>
                                                <NavLink className="main-text">Mes RDVs</NavLink>
                                                </Link>
                                                <NavLink className="main-text" href="/">Deconnexion</NavLink>
                                                </div>
                                            }           
                                        </NavItem> 
                                    </Nav>
                                :
                                    <Nav>
                                            {props.token === ''?
                                                <NavLink className="main-text" href="/identification">Se Connecter/S'inscrire</NavLink>
                                                :
                                                <div style={{display: 'flex', justifyContent: 'center'}}>
                                                 <Link to='/rdv'>
                                                <NavLink className="main-text">Mes RDVs</NavLink>
                                                </Link>
                                                <NavLink className="main-text" href="/">Deconnexion</NavLink>
                                                </div>
                                            }  
                                    </Nav>    
                        }
                        
                    </Collapse>
                </div>
            </Navbar>
        </div>
    )
}


function mapStateToProps(state) {
    return { token: state.token };
  }
  
  
  
    
    export default connect(mapStateToProps, null)(NavBar);
